@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  margin: 0;
  font-family: "Lato", "Arial", sans-serif;
  font-weight: 400;
  background-color: #ffffff;
  color: #000000;
}

.page__body {
  font-size: 14px;
  line-height: 18px;
}

.container {
  min-width: 320px;
  padding: 0 20px;
}

.main-header {
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
  padding-bottom: 17px;
}

.main-header__logo {
  max-width: 64%;
}

.choose-program {
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
}

.choose-program__wrapper {
  padding: 0 0 20px 0;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.choose-program__content {
  padding: 27px 0 0 0;
  margin: 0 -30px 0 -30px;
  min-height: 302px;
  background-color: #68b738;
  background-image: url("../img/index-bg.png"), url("../img/index-background.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.choose-program__title {
  padding: 0 20px 0 20px;
  margin: 0 0 25px 0;
}

.choose-program__slogan {
  line-height: 14px;
  text-transform: uppercase;
}

.choose-program__image-wrapper {
  margin-top: -135px;
}

.main-container {
  text-align: center;
}

.main-container__title {
  font-family: "Oswald", "Arial", sans-serif;
  margin: 27px 0 40px;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
}

.program {
  margin-top: 20px;
}

.program__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.program-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.program-list__item {
  padding: 20px 22px 20px 22px;
  margin: 0 0 20px 0;
  background-color: #f2f2f2;
}

.program-list__wrapper {
  display: flex;
  align-items: center;
}

.program-list__icon-background {
  margin-right: 19px;
  position: relative;
  width: 50px;
  height: 50px;
  background-color: #68b738;
  border-radius: 50%;
}

.program-list__icon {
  position: absolute;
}
.program-list__icon--slim {
  left: 0;
}
.program-list__icon--pro {
  top: 15px;
  left: 0;
}

.program-list__title {
  font-family: "Oswald", "Arial", sans-serif;
  margin: 0;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
}

.program-list__description {
  margin: 0;
  padding: 22px 0;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  text-align: start;
}

.program-list__link {
  font-family: "Oswald", "Arial", sans-serif;
  display: block;
  padding: 15px 47px 0 0;
  font-size: 16px;
  line-height: 16px;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
  text-align: start;
}

.program-list__arrow {
  margin-left: 15px;
}

.about {
  margin-top: 20px;
  padding-bottom: 50px;
}

.about__title {
  text-align: start;
}

.about__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.about__item {
  display: flex;
  align-items: center;
}

.about__icon {
  margin: 0 20px 0 0;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: center;
}
.about__icon--leaf {
  background-image: url("../img/icons/icon-leaf.svg");
}
.about__icon--powder {
  background-image: url("../img/icons/icon-powder.svg");
}
.about__icon--eat {
  background-image: url("../img/icons/icon-eat.svg");
}
.about__icon--time {
  background-image: url("../img/icons/icon-time.svg");
}

.about__description {
  color: #444444;
}

.results {
  background-color: #eaeaea;
}

.results__wrapper {
  padding: 25px 0 40px;
}

.results__title {
  margin: 0 0 40px 0;
  text-align: start;
}

.results__description {
  color: #444444;
  text-align: start;
}

.results__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  margin: 0 0 34px 0;
  display: flex;
  justify-content: space-between;
}

.results__item {
  padding: 14px 0 0 0;
  position: relative;
  display: flex;
  flex-direction: column;
  text-align: center;
  border: 1px solid #cdcdcd;
  border-radius: 2px;
}

.results__item-title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 24px;
}

.results__item-description {
  margin-top: 5px;
  font-size: 12px;
  line-height: 12px;
  color: #444444;
}

.results__text {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
}

.results__image {
  height: 256px;
  padding: 0 0 40px 0;
}

.slider__items {
  height: 100%;
  display: flex;
  justify-content: center;
}

.slider__image {
  width: 162px;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}
.slider__image--before {
  background-image: url("../img/mobile/cat-before-mobile.png");
  background-position: -18% 50%;
}
.slider__image--after {
  background-image: url("../img/mobile/cat-after-mobile.png");
  background-position: 120% 50%;
}

.main-footer__wrapper {
  padding-top: 40px;
  padding-bottom: 40px;
}

.main-footer__logo {
  display: block;
  padding-bottom: 20px;
  text-align: center;
}

.main-footer__social {
  padding: 20px;
}

.main-footer__copyright {
  padding-top: 20px;
}

.dealer__wrapper {
  padding: 26px 0;
  display: flex;
  align-items: center;
}

.dealer__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: start;
  color: #111111;
  text-transform: uppercase;
}

.dealer__address {
  max-width: 50%;
  font-size: 14px;
  line-height: 20px;
  color: #444444;
  font-style: normal;
}

.social__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-evenly;
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  width: 28px;
  height: 22px;
}
.social__link--vkontakte {
  background-image: url("../img/icons/vkontakte.svg");
}
.social__link--youtube {
  background-image: url("../img/icons/youtube.svg");
}
.social__link--telegram {
  background-image: url("../img/icons/telegram.svg");
}

.button {
  font-family: "Oswald", "Arial", sans-serif;
  display: block;
  padding: 10px 0;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
  background-color: #68b738;
  border: none;
}
.button--additional {
  font-size: 16px;
  line-height: 20px;
  color: #444444;
  background-color: #f2f2f2;
}
.button--submit {
  font-size: 16px;
  line-height: 20px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.catalog__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.card {
  margin: 0;
  padding: 0;
  list-style-type: none;
  padding: 11px 0 14px;
}
.card:not(:last-child) {
  margin-bottom: 19px;
}

.card__wrapper {
  display: flex;
  margin-bottom: 13px;
}

.card__image-link {
  padding: 14px 36px 12px;
}
.card__image-link--full {
  padding: 11px 28px 1px;
}

.card__info {
  margin-bottom: 9px;
}
.card__info--more {
  margin: 0 auto;
}

.card__link {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  margin-bottom: 18px;
  text-decoration: none;
}

.card__list {
  display: flex;
  flex-direction: column;
  padding: 0;
}

.card__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
  text-decoration: none;
}

.card__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  font-weight: 400;
  color: #222222;
}
.card__title--more {
  color: #111111;
}

.card__subtitle {
  padding: 0;
  margin: 5px 0 0;
  font-size: 12px;
  line-height: 16px;
  color: #444444;
}

.additional {
  margin-bottom: 19px;
}

.additional__title {
  margin: 25px 0 36px;
  text-align: start;
}

.additional__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  padding: 0 0 19px;
}

.additional__item {
  font-family: "Oswald", "Arial", sans-serif;
  color: #222222;
}
.additional__item:not(:last-child) {
  margin-bottom: 19px;
}

.additional__button {
  margin-top: 15px;
}

.present {
  padding: 59px 60px 10px;
  min-height: 302px;
  background-color: #68b738;
  background-image: url("../img/green-bg.png"), url("../img/present-cat.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.present__icon {
  margin: 0 auto;
  width: 78px;
  height: 80px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../img/icons/icon-gift.svg");
}

.present__description {
  margin: 48px 0 0;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
}

.copyright__link {
  width: 100%;
  padding: 10px 0 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  text-decoration: none;
}

.item {
  padding-top: 13px;
}

.item__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  color: #222222;
  text-transform: uppercase;
  text-align: start;
}

.item__info {
  display: flex;
  justify-content: space-between;
  margin-top: 13px;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  color: #444444;
  text-align: start;
}

.form {
  font-family: "Oswald", "Arial", sans-serif;
  padding-top: 27px;
}

.form__title {
  margin: 0 0 37px;
}

.form__subtitle {
  margin: 0 0 39px;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-align: start;
}

.form__group {
  display: flex;
  flex-direction: column;
  align-items: start;
  padding-bottom: 24px;
  color: #444444;
}
.form__group--radio {
  padding-bottom: 19px;
  color: #222222;
}

.form__group-title {
  margin-bottom: 35px;
  color: #000000;
  text-align: start;
}

.form__group-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: start;
}

.form__legend {
  margin: 0;
  padding-top: 21px;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  text-transform: none;
}

.form-field {
  display: flex;
  width: 100%;
  margin-bottom: 13px;
  align-items: center;
}

.form-field__label {
  flex-shrink: 0;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  text-transform: uppercase;
}
.form-field__label--base-color {
  color: #000000;
}
.form-field__label--additional-color {
  color: #222222;
}

.form-field__control {
  flex-grow: 1;
  margin-left: 13px;
  padding: 14px 20px 11px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  border: 1px solid #e7e7e7;
  text-transform: uppercase;
  min-width: 173px;
}
.form-field__control--radio {
  flex-grow: 0;
  width: 30px;
  margin: 0 15px 0 0;
  min-width: auto;
}
.form-field__control--textarea {
  margin: 0;
  padding: 15px 9px 12px;
  min-height: 150px;
}
.form-field__control--checkbox {
  flex-grow: 0;
  margin: 0 16px 0 0;
  min-width: auto;
}
.form-field__control--phone {
  padding: 14px 12px 13px 15px;
}
.form-field__control--email {
  padding: 14px 12px 13px 15px;
}
.form-field__control::placeholder {
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
}

.site-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
}

.site-list__item {
  font-family: "Oswald", "Arial", sans-serif;
  padding: 22px 10px 24px;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-decoration: none;
}

.site-list__link {
  color: #000000;
  text-decoration: none;
}

.main-nav__toggle {
  width: 40px;
  height: 40px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.main-nav__toggle-line {
  height: 1.5px;
  width: 24px;
  background-color: #000000;
}

.main-nav__toggle-line::before {
  content: "";
  height: 2px;
  width: 24px;
  position: absolute;
  top: 11px;
  left: 8px;
  background-color: #000000;
}

.main-nav__toggle-line::after {
  content: "";
  height: 2px;
  width: 24px;
  position: absolute;
  bottom: 11px;
  left: 8px;
  background-color: #000000;
}

.main-nav__list--closed {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  z-index: 1;
  background-color: #ffffff;
}

.logo__wrapper {
  display: flex;
  justify-content: space-between;
}

.logo__text--header {
  margin-left: 40%;
}

.form-group__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.title {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
}
.title--h1 {
  font-size: 36px;
  line-height: 36px;
}
.title--h2 {
  font-size: 36px;
  line-height: 40px;
}
.title--h3 {
  font-size: 24px;
  line-height: 26px;
}
.title--small {
  font-size: 24px;
  line-height: 24px;
}